@charset "UTF-8";
.rw-checkbox {
  white-space: nowrap;
  cursor: pointer;
  outline: none;
  display: inline-block;
  position: relative;
  line-height: 1;
  vertical-align: middle; }
  .rw-checkbox:hover .rw-checkbox-inner,
  .rw-checkbox-input:focus + .rw-checkbox-inner {
    border-color: #3dbcf6; }
  .rw-checkbox-inner {
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    position: relative;
    top: 0;
    left: 0;
    display: inline-block;
    width: 14px;
    height: 14px;
    border-width: 1px;
    border-style: solid;
    border-radius: 0;
    border-color: #bbb;
    background-color: #ffffff;
    vertical-align: top; }
    .rw-checkbox-inner:after {
      -webkit-transition: all .3s;
      -o-transition: all .3s;
      transition: all .3s;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
      position: absolute;
      left: 4px;
      top: 1px;
      display: table;
      width: 5px;
      height: 8px;
      border: 2px solid #ffffff;
      border-top: 0;
      border-left: 0;
      content: ' '; }
  .rw-checkbox-input {
    position: absolute;
    left: 0;
    z-index: 9999;
    cursor: pointer;
    opacity: 0;
    top: 0;
    bottom: 0;
    right: 0; }

/* 选中状态 */
.rw-checkbox-checked:hover .rw-checkbox-inner {
  border-color: #3dbcf6; }

.rw-checkbox-checked .rw-checkbox-inner {
  border-color: #3dbcf6;
  background-color: #3dbcf6; }
  .rw-checkbox-checked .rw-checkbox-inner:after {
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    position: absolute;
    left: 4px;
    top: 1px;
    display: table;
    width: 5px;
    height: 8px;
    border: 2px solid #ffffff;
    border-top: 0;
    border-left: 0;
    content: ' '; }

/* 半选中状态 */
.rw-checkbox-indeterminate .rw-checkbox-inner {
  border-color: #3dbcf6;
  background-color: #FFF; }
  .rw-checkbox-indeterminate .rw-checkbox-inner:after {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
    position: absolute;
    background-color: #3dbcf6;
    left: 50%;
    top: 50%;
    margin-left: -3px;
    margin-top: -3px;
    display: table;
    width: 6px;
    height: 6px;
    border: none;
    content: ' '; }

.rw-checkbox-disabled.rw-checkbox-checked:hover .rw-checkbox-inner {
  border-color: #d9d9d9; }

.rw-checkbox-disabled.rw-checkbox-checked .rw-checkbox-inner {
  background-color: #f3f3f3;
  border-color: #d9d9d9; }
  .rw-checkbox-disabled.rw-checkbox-checked .rw-checkbox-inner:after {
    border-color: #cccccc; }

.rw-checkbox-disabled:hover .rw-checkbox-inner {
  border-color: #d9d9d9; }

.rw-checkbox-disabled .rw-checkbox-inner {
  border-color: #d9d9d9;
  background-color: #f3f3f3; }
  .rw-checkbox-disabled .rw-checkbox-inner:after {
    border-color: #f3f3f3; }

.rw-checkbox-disabled .rw-checkbox-inner-input {
  cursor: default; }

.rw-checkbox-wrapper {
  cursor: pointer;
  font-size: 12px;
  display: inline-block;
  vertical-align: middle; }
  .rw-checkbox-wrapper .rw-checkbox-label {
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    padding-left: 5px;
    padding-right: 5px;
    vertical-align: middle; }

.rw-checkbox-wrapper:not(:last-child) {
  margin-right: 8px; }

.rw-checkbox-disabled + .rw-checkbox-label {
  color: rgba(0, 0, 0, 0.4);
  cursor: not-allowed; }
